.play-area-container {
    position: fixed;
    top: 0;
    right: 0;
    width: 25vw;
    max-width: 500px;
    min-width: 220px;
    height: 100%;
    padding: 15px;
    box-shadow: -5px 0 30px 5px rgba(255, 255, 255, 0.05);;
}

.tabs {
    margin-bottom: 20px;
    padding: 10px;
}
.tabs > li {
    display: inline-block;
    margin-right: 20px;
    padding-bottom: 10px;
    border-bottom: 1px solid #FFF;
    font-size: 14px;
    cursor: pointer;
}
.tabs > li:first-of-type {
    color: var(--red);
    font-weight: bold;
    border-bottom: 2px solid var(--red);
}

.album-cover-wrap {
    width: 20vw;
    height: 20vw;
    margin: 0 auto 30px;
    border: 1px solid rgba(255, 255, 255, 0.1);
}
.album-cover-wrap > img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.played-music-info {
    margin-bottom: 30px;
    padding-bottom: 30px;
    text-align: center;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
.played-music-info > .title {
    margin-bottom: 10px;
    font-size: 18px;
    font-weight: bold;
}
.played-music-info > .artist {
    font-size: 13px;
    color: var(--cloud);
}

.lyrics {
    width: 20vw;
    height: 200px;
    margin: 0 auto;
    font-size: 13px;
    line-height: 20px;
    overflow-y: auto;
}
.lyrics::-webkit-scrollbar {
    width: 8px;
}
.lyrics::-webkit-scrollbar-thumb {
    background-color: var(--smoke);
    border-radius: 10px;
}
.lyrics::-webkit-scrollbar-track {
    background-color: var(--cloud);
    border-radius: 10px;
}